/* tslint:disable: ordered-imports*/
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

/* Module */
import { FormModule } from './forms.module';

/* Containers */
import * as tablesContainers from './containers';

/* Guards */
import * as tablesGuards from './guards';
import { SBRouteData } from '@modules/navigation/models';

/* Routes */
export const ROUTES1: Routes = [
    {
        path: '',
        canActivate: [],
        component: tablesContainers.TablesComponent,
        data: {
            title: 'Form',
            breadcrumbs: [
                {
                    text: 'Dashboard',
                    link: '/dashboard',
                },
                {
                    text: 'Forms',
                    active: true,
                },
            ],
        } as SBRouteData,
    },
];

@NgModule({
    imports: [FormModule, RouterModule.forChild(FormsRoutingModule.Route)],
    exports: [RouterModule],
})
export class FormsRoutingModule {
    constructor(){
    }
    public static Data: SBRouteData = {
        title: localStorage.getItem('title')!,
        breadcrumbs: [
            {
                text: 'Dashboard',
                link: '/dashboard',
            },
            {
                text: 'Form',
                active: true,
            },
        ],
    }
    public static Route: Routes = [
        {
            path: '',
            canActivate: [],
            component: tablesContainers.TablesComponent,
            data: FormsRoutingModule.Data,
            children: [
                {path: '', component: tablesContainers.TablesComponent, data: FormsRoutingModule.Data},
                {
                    path: ':idmenu',
                    component: tablesContainers.TablesComponent,
                    data: FormsRoutingModule.Data,
                    children: [
                        {path: '', component: tablesContainers.TablesComponent, data: FormsRoutingModule.Data},
                        {
                            path: ':id',
                            component: tablesContainers.TablesComponent,
                            data: FormsRoutingModule.Data,
                            children: [
                                {path: '', component: tablesContainers.TablesComponent, data: FormsRoutingModule.Data},
                                {
                                    path: ':op',
                                    component: tablesContainers.TablesComponent,
                                    data: FormsRoutingModule.Data
                                }
                            ]
                        }
                    ]
                }
            ]
        }
];
}
