import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    Input,
    OnInit,
    QueryList,
    ViewChildren,
} from '@angular/core';
import { FormGroup, FormControl, Validators, FormArray } from '@angular/forms';
import { Router,ActivatedRoute } from '@angular/router';
import { SBSortableHeaderDirective, SortEvent } from '@modules/details/directives';
// import { Country } from '@modules/details/models';
import { TableData, MetaData } from '@modules/services/base.model';
import { CoreService } from '@modules/services/base.service';
import { DataService } from '@modules/details/services';
import { BehaviorSubject, Observable, of, Subject } from 'rxjs';
import { AppParameter, NavIdentification, ButtonStatus } from '@app/app.parameter';

@Component({
    selector: 'sb-ng-bootstrap-table',
    changeDetection: ChangeDetectionStrategy.OnPush,
    templateUrl: './ng-bootstrap-table.component.html',
    styleUrls: ['ng-bootstrap-table.component.scss'],
})
export class NgBootstrapTableComponent implements OnInit {
    @Input() pageSize = 4;

    titles: string[] = ['Kode','Uraian']
    columns: string[] = ['name','area']
    data!: TableData;
    metadata: MetaData[] = [];

    form!: FormGroup;

    btnStatus!: ButtonStatus;
    lastError!: string;
    idmenu!:number;
    id!:string;
    op!:string;
    data$!: Observable<any[]>;
    total$!: Observable<number>;
    sortedColumn!: string;
    sortedDirection!: string;
    baseURL: string = '';

    @ViewChildren(SBSortableHeaderDirective) headers!: QueryList<SBSortableHeaderDirective>;

    constructor(
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private param: AppParameter,
        private coreService: CoreService,
        public dataService: DataService,
        private changeDetectorRef: ChangeDetectorRef
    ) {
        this.ngOnInit();
    }

    ngOnInit() {
        // Intialization
        this.idmenu = this.coreService.GetIdmenu();
        const url = this.router.url;
        // console.log(url);
        let splitted = url.split("/");
        if(splitted.length === 4){
            this.id = splitted[splitted.length-1];
            sessionStorage.setItem("id", this.id);
        }else{
            this.id = sessionStorage.getItem("id")??"";
        }

        const json = sessionStorage.getItem(this.idmenu.toString());
        if(json!=null){
            const md: TableData = JSON.parse(json);
            this.metadata = md.metadata;
            // console.log(md);
            this.form = new FormGroup({
                SubStageFormArray : new FormArray([])
            });
            const fa = this.form.get('SubStageFormArray') as FormArray;
            const arrValids : any[] = [];
            const vals = md.values.find(o=>o.id === parseInt(this.id, 0));
            // tslint:disable-next-line: prefer-for-of
            for (let i=0; i<this.metadata.length ; i++){
                const item = this.metadata[i];
                if(!!vals){
                    fa.push(new FormControl(vals[item.name], arrValids));
                }else{
                    fa.push(new FormControl("", arrValids));
                }
            }
        }

        // On URL Changed Event
        this.param.receiveFunctionEvent().subscribe(
            (p: NavIdentification) => {
                splitted = p.url.split("/");
                if(splitted.length === 4){
                    this.id = splitted[splitted.length-1];
                    sessionStorage.setItem("id", this.id);
                }else{
                    this.id = sessionStorage.getItem("id")??"";
                }
                this.op = p.op;
                this.btnStatus = {
                    mode : p.op?p.op:'view',
                    isEditing : (p.op === 'add') || (p.op === 'edit'),
                    isNew : (p.op === 'add')
                };
                if(!!this.id){

                    // TODO optimize to only get a row data, without metadata
                    this.coreService.GetMetaData().then(
                        md => {
                            this.data=md;
                            this.metadata = md.metadata;

                            this.form = new FormGroup({
                                SubStageFormArray : new FormArray([])
                            }, { updateOn: 'change' });

                            const fa = this.form.get('SubStageFormArray') as FormArray;
                            // console.log(fa);
                            const arrValids : any[] = [];
                            const vals = md.values.find(o=>o.id === parseInt(this.id, 0));
                            // tslint:disable-next-line: prefer-for-of
                            for (let i=0; i<this.metadata.length ; i++){
                                const item = this.metadata[i];
                                if(!!vals){
                                    fa.push(new FormControl(vals[item.name], arrValids));
                                }else{
                                    fa.push(new FormControl("", arrValids));
                                }
                                // console.log(item.name+'='+vals[item.name]);
                            }

                            this.dataService.pageSize = this.pageSize;
                            this.data$ = this.dataService.data$;
                            this.total$ = this.dataService.total$;

                            this.baseURL = this.coreService.getBaseURL();
                        }
                    )
                    ;
                }

            }
        );
    }

    onSort({ column, direction }: SortEvent) {
        this.sortedColumn = column;
        this.sortedDirection = direction;
        this.dataService.sortColumn = column;
        this.dataService.sortDirection = direction;
        this.changeDetectorRef.detectChanges();
    }
    onSubmit() {
        const array = this.form.get('SubStageFormArray') as FormArray;

        const form : Dictionary<string> = {};
        const vals = this.data.values.find(o=>o.id === parseInt(this.id, 0));
        const values = this.form.value.SubStageFormArray;
        // tslint:disable-next-line: prefer-for-of
        for(let i=0;i<this.metadata.length;i++){
            form[this.metadata[i].name] = values[i];
            vals[this.metadata[i].name] = values[i];
        }
        const json = JSON.stringify(form);
        // console.log(form);
        this.coreService.GetMetaData().then(
            metadata => {
                // console.log(metadata);
                this.data = metadata;
                sessionStorage.setItem(this.idmenu.toString(), JSON.stringify(metadata));
                // const fa = this.form.get('SubStageFormArray') as FormArray;
                // // console.log(fa);
                // const arrValids : any[] = [];
                // // tslint:disable-next-line: no-shadowed-variable
                // const vals = this.data.values.find(o=>o.id === parseInt(this.id, 0));
                // // tslint:disable-next-line: prefer-for-of
                // for (let i=0; i<this.metadata.length ; i++){
                //     const item = this.metadata[i];
                //     fa.push(new FormControl(vals[item.name], arrValids));
                //     console.log(item.name+'='+vals[item.name]);
                // }
            }
        );
        this.onSave(form);
    }
    onAdd(){
        const route = `${this.baseURL}/forms/${this.idmenu}/${this.id}/add/`;
        this.router.navigate([route]);
    }
    onEdit(){
        const route = `${this.baseURL}/forms/${this.idmenu}/${this.id}/edit/`;
        this.router.navigate([route]);
    }
    onDelete(){
        const route = `${this.baseURL}/details/${this.idmenu}`;
        this.router.navigate([route]);
    }
    onSave(param: any){
        this.coreService.Save(param);

        let route = `${this.baseURL}/forms/${this.idmenu}/${this.id}/`;
        if(this.btnStatus.isNew){
            this.id = '-1'; // newid
            if(false){// failed
                route = `${this.baseURL}/details/${this.idmenu}`;
            }
        }else{

        }
        // this.router.routeReuseStrategy.shouldReuseRoute = () => false;
        // this.router.onSameUrlNavigation = 'reload';
        this.router.navigate([route]);


        // this.router.navigate([route]).then(() => {
        //     window.location.reload();
        // });;
    }
    onCancel(){
        console.log(this.idmenu);
        let route = `${this.baseURL}/forms/${this.idmenu}/${this.id}/`;
        if(this.btnStatus.isNew){
            route = `${this.baseURL}/details/${this.idmenu}`;
        }
        this.router.navigate([route]);
    }
    onBack(){
        const route = `${this.baseURL}/details/${this.idmenu}`;
        this.router.navigate([route]);
    }

    get SubStageValues() : FormArray {
        return this.form.get('SubStageFormArray') as FormArray;
    }
    IsValidItem(indexNo: number){
        // console.log(this.form);
        // const fc : FormControl = <FormControl>this.SubStageValues.at(indexNo);
        // if(fc!=null && fc.errors !=null && fc.touched && fc.dirty)
        // {
        //   this.lastError = "no error";
        //   return true;
        //   // return !(fc.errors.required || fc.errors.min || fc.errors.max || fc.errors.minlength || fc.errors.maxlength);
        // }
        // else
        // {
        //   this.lastError = "no error";
        //   return true;
        // }
      }
}

interface Dictionary<T> {
    [Key: string]: T;
}

