import { Router, ActivatedRoute, Event as NavigationEvent, NavigationStart, NavigationEnd } from '@angular/router';
import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { AppParameter, NavIdentification } from '@app/app.parameter';
import { CoreService } from '@modules/services/base.service';
import { DataService } from '@modules/details/services';
import { filter } from 'rxjs/operators';

@Component({
    selector: 'sb-details',
    changeDetection: ChangeDetectionStrategy.OnPush,
    templateUrl: './tables.component.html',
    styleUrls: ['tables.component.scss'],
})
export class TablesComponent implements OnInit {
    Title!: string;
    Description!: string;

    constructor(
        public router: Router,
        private activatedRoute: ActivatedRoute,
        private param: AppParameter,
        private coreService: CoreService,
        public dataService: DataService,
    ) {
        this.router.events
            .pipe(
                filter(
                    (event: NavigationEvent ) => {
                        return(event instanceof NavigationEnd );
                    }
                )
            )
            .subscribe((event) => {
                const e = event as NavigationEnd;
                const navId = new NavIdentification();
                navId.url =  e.url.toString();
                const pars = navId.url.split("/");
                const temp = pars[pars.length-1].toString();
                // console.log(['add', 'edit', 'view'].includes(temp));
                if(['add', 'edit', 'view'].includes(temp)){
                    navId.op = pars[pars.length-1];
                }else{
                    navId.op = 'view';
                }
                // console.log(navId);
                this.param.sendFunctionEvent(navId);

                if(this.coreService.CanGetData()){
                    this.coreService.GetMetaData().then(
                        metadata => {
                            this.Title = metadata.title;
                            this.Description = metadata.description;
                        }
                    );
                }
            });
    }
    ngOnInit() {
    }
}
