import { DecimalPipe } from '@angular/common';
import { Injectable, PipeTransform } from '@angular/core';
import { ActivatedRoute, ActivatedRouteSnapshot, Router, RouterStateSnapshot, NavigationEnd } from '@angular/router';
import { SortDirection } from '@modules/details/directives';
import { BehaviorSubject, Observable, of, Subject } from 'rxjs';
import { debounceTime, delay, switchMap, tap } from 'rxjs/operators';
import { CoreService } from '@modules/services/base.service';
import { AppParameter } from '@app/app.parameter';
import { TableData } from '@modules/services/base.model';


interface SearchResult {
    data: any[];
    total: number;
}

interface State {
    page: number;
    pageSize: number;
    searchTerm: string;
    sortColumn: string;
    sortDirection: SortDirection;
}

function compare(v1: number | string, v2: number | string) {
    return v1 < v2 ? -1 : v1 > v2 ? 1 : 0;
}

function sort(data: any[], column: string, direction: string): any[] {
    if (direction === '') {
        return data;
    } else {
        return [...data].sort((a, b) => {
            const res = compare(a[column], b[column]);
            return direction === 'asc' ? res : -res;
        });
    }
}

function matches(country: any, term: string, pipe: PipeTransform) {
    return (
        country.name.toLowerCase().includes(term.toLowerCase()) ||
        pipe.transform(country.area).includes(term) ||
        pipe.transform(country.population).includes(term)
    );
}

@Injectable({ providedIn: 'root' })
export class DataService {
    private _loading$ = new BehaviorSubject<boolean>(true);
    private _search$ = new Subject<void>();
    private _data$ = new BehaviorSubject<any[]>([]);
    private _total$ = new BehaviorSubject<number>(0);

    private _state: State = {
        page: 1,
        pageSize: 4,
        searchTerm: '',
        sortColumn: '',
        sortDirection: '',
    };
    idmenu!:string;
    constructor(
        private param: AppParameter,
        private coreService: CoreService,
        private pipe: DecimalPipe) {

        const id = '';
        const url = '';

        this.param.receiveFunctionEvent().subscribe(
            p => {
                if(p.idmenu !== ''){
                    this.idmenu = p.idmenu;

                    if(this.coreService.CanGetData()){
                        this.coreService.GetMetaData().then(
                         metadata => {
                            this._search$
                            .pipe(
                                tap(() => this._loading$.next(true)),
                                debounceTime(120),
                                switchMap(() => this._search(metadata)),
                                delay(120),
                                tap(() => this._loading$.next(false))
                            )
                            .subscribe(result => {
                                this._data$.next(result.data);
                                this._total$.next(result.total);
                            });

                            this._search$.next();
                        }
                    );
                    }
                }
            }
        );
    }
    get data$() {
        return this._data$.asObservable();
    }
    get total$() {
        return this._total$.asObservable();
    }
    get loading$() {
        return this._loading$.asObservable();
    }
    get page() {
        return this._state.page;
    }
    set page(page: number) {
        this._set({ page });
    }
    get pageSize() {
        return this._state.pageSize;
    }
    set pageSize(pageSize: number) {
        this._set({ pageSize });
    }
    get searchTerm() {
        return this._state.searchTerm;
    }
    set searchTerm(searchTerm: string) {
        this._set({ searchTerm });
    }
    set sortColumn(sortColumn: string) {
        this._set({ sortColumn });
    }
    set sortDirection(sortDirection: SortDirection) {
        this._set({ sortDirection });
    }

    private _set(patch: Partial<State>) {
        Object.assign(this._state, patch);
        this._search$.next();
    }

    private _search(metadata: TableData): Observable<SearchResult> {

        const { sortColumn, sortDirection, pageSize, page, searchTerm } = this._state;

        // 1. sort
        let data = sort(metadata.values, sortColumn, sortDirection);

        // // 2. filter
        data = data.filter(d => matches(d, searchTerm, this.pipe));
        const total = data.length;

        // 3. paginate
        data = data.slice((page - 1) * pageSize, (page - 1) * pageSize + pageSize);
        return of({ data, total });
    }
}
